<script>
import authorization from "@/utils/authorization";
import BlogHeader from "@/components/BlogHeader.vue";
import BlogFooter from "@/components/BlogFooter.vue";
import axios from "axios";

const storage = localStorage;

export default {
  name: 'UserCenter',
  components: {BlogHeader, BlogFooter},
  data: function () {
    return {
      username: '',
      password: '',
      token: '',
      welcomeName: '',
      showingDeleteAlert: false,
    }
  },
  methods: {
    changeInfo() {
      const that = this;
      authorization().then(function (response) {
        if (!response[0]) {
          alert('登陆已过期');
          return
        }
        console.log('change info start');
        if (that.password.length > 0 && that.password.length < 6) {
          alert('password too short');
          return;
        }
        const oldName = storage.getItem('username.myblog');
        let data = {};
        if (that.username !== '') {
          data.username = that.username
        }
        if (that.password !== '') {
          data.password = that.password
        }
        that.token = storage.getItem('access.myblog');
        axios.patch(
            '/api/user/' + oldName + '/',
            data,
            {
              headers: {Authorization: 'Bearer' + that.token}
            }
        )
            .then(function (response) {
              const name = response.data.username;
              storage.setItem('username.myblog', name);
              that.$router.push({name: 'UserCenter', params: {username: name}});
              that.welcomeName = name;
            })
      });
    },
    confirmDelete() {
      const that = this;
      authorization()
          .then(function (response) {
            if (response[0]) {
              // 获取令牌
              that.token = storage.getItem('access.myblog');
              axios
                  .delete('/api/user/' + that.username + '/', {
                    headers: {Authorization: 'Bearer ' + that.token}
                  })
                  .then(function () {
                    storage.clear();
                    that.$router.push({name: 'Home'});
                  })
            }
          })
    },
  },
  mounted() {
    this.welcomeName = storage.getItem('username.myblog');
  }
}

</script>

<template>
  <BlogHeader :welcome-name="welcomeName"/>
  <div id="user-center">
    <h3>更新资料信息</h3>
    <form>
      <div class="form-elem">
        <span>用户名:</span>
        <input v-model="username" type="text" placeholder="输入用户名">
      </div>
      <div class="form-elem">
        <span>新密码:</span>
        <input v-model="password" type="password" placeholder="输入新密码">
      </div>

      <div class="form-elem">
        <button v-on:click.prevent="changeInfo">更新</button>
      </div>

      <div class="form-elem">
        <button
            v-on:click.prevent="showingDeleteAlert = true"
            class="delete-btn"
        >删除用户
        </button>
        <div :class="{ shake: showingDeleteAlert }">
          <button
              v-if="showingDeleteAlert"
              class="confirm-btn"
              @click.prevent="confirmDelete"
          >确定？
          </button>
        </div>
      </div>
    </form>

  </div>
  <BlogFooter/>
</template>

<style scoped>
#user-center {
  text-align: center;
}

.form-elem {
  padding: 10px;
}

input {
  height: 25px;
  padding-left: 10px;
}

button {
  height: 35px;
  cursor: pointer;
  border: none;
  outline: none;
  background: gray;
  color: whitesmoke;
  border-radius: 5px;
  width: 200px;
}

.confirm-btn {
  width: 80px;
  background-color: darkorange;
}

.delete-btn {
  background-color: darkred;
  margin-bottom: 10px;
}

.shake {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }
  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}
</style>